import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const login=()=>import("../components/login")
const index=()=>import("../components/index")

export default new Router({
  routes: [
   {
     path:"/",
     component:login
   },
   {
     path:"/index",
     component:index
   },
   {
     path:"*",
     redirect:"/login"
   }
  ]
})



